@import "https://fonts.googleapis.com/css?family=Roboto:400,400i,700";
body {
  background-color: #333c4c;
  justify-content: center;
  align-items: center;
  height: 100dvh;
  margin: 0;
  font-family: Roboto, sans-serif;
  display: flex;
}

.card {
  background: #fff;
  border-radius: 25px;
  flex-direction: row;
  width: 400px;
  max-width: 400px;
  max-height: 400px;
  display: flex;
  position: relative;
}

.card h3, h1 {
  color: #2999c9;
  margin: 0;
  padding: 0 1rem 0 1.5rem;
}

.card h3 {
  height: 19.2px;
  font-size: 16px;
}

h1 {
  color: #333c4c;
}

.card .title {
  color: #2999c9;
  height: 15.2px;
  padding-left: 1.5rem;
  font-size: 13px;
  font-weight: bold;
}

.card .desc {
  border-right: 2px solid #2999c9;
  margin-left: .3rem;
  padding: .2rem .5rem;
  font-size: 10px;
}

.card .nip {
  padding-bottom: .5rem;
  padding-left: 1.5rem;
  font-size: 10px;
}

.card .actions {
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  padding: .5rem 1rem;
  display: grid;
}

.card svg {
  width: 85px;
  height: 85px;
  margin: 0 auto;
}

.img-avatar {
  background-color: #2999c9;
  border: 6px solid #fff;
  border-radius: 50%;
  width: 84px;
  height: 80px;
  position: absolute;
  top: 20px;
  left: 30px;
  overflow: hidden;
}

.img-avatar > img {
  width: 100%;
  height: 100%;
  display: block;
}

.hide {
  display: none !important;
}

.card-text {
  grid-template-columns: 1fr 2fr;
  display: grid;
}

.title-total {
  width: 275px;
  padding: 1.5em 1.5em 1.5em .5em;
}

path {
  fill: #fff;
}

.img-portada {
  width: 100%;
}

.portada {
  background-color: #2999c9;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  width: 100%;
  height: 100%;
}

button {
  color: #8bc34a;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 24px;
  transition: all .5s;

  &:hover {
    transform: rotate(22deg);
  }
}

.desc a, a:visited {
  color: #2999c9;
}

.actions button:hover > * {
  color: #4caf50;
}

.actions a {
  color: #8bc34a;
  text-decoration: none;
}

.footer {
  text-align: right;
  align-items: center;
  padding-top: .5rem;
  padding-left: 1.5rem;
  display: flex;
}

.card .logo-bsre {
  width: 26.4px;
  height: 30px;
  padding-left: .2rem;
}

.icons {
  padding-top: 1rem;
  padding-bottom: 1rem;
  padding-left: 1.5rem;
}

.icons > div {
  align-items: center;
  margin: .2rem 0;
  display: flex;
}

.icons i {
  color: #fff;
  border-radius: 25%;
  padding: .1rem .2rem;
}

.icons > div:first-child > i {
  background-color: #4caf50;
}

.icons > div:nth-child(2) > i {
  background-color: red;
  padding: .2rem;
  font-size: 13px;
}

.icons > div:nth-child(3) > i {
  background-color: #2999c9;
}

.icons a {
  padding-left: .5rem;
  font-size: 10px;
}
